import React from "react";

function MovieTemplate({ title, summary, img, rating, genres }) {
  return (
    <>
      <div className="movie">
        <img src={img} alt={img} title={title} />
        <div className="movie_data">
          <h3 className="movie_title">{title}</h3>
          <h4>
            <span>장르 : </span>
            {genres.map((item) => (
              <span key={item}>{item} </span>
            ))}
          </h4>
          <div className="movie_rating">평점 : {rating}</div>
          <p className="movies_summary">
            줄거리
            <br />
            {summary.slice(0, 100)}...
          </p>
        </div>
      </div>
      <hr />
    </>
  );
}

export default MovieTemplate;
